@keyframes scale-star-compact {
  0% {
    background-size: 1.0625rem;
  }
  100% {
    background-size: 350%;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
@keyframes fade-up {
  0% {
    transform: translateY(25px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes fade-up-margins {
  0% {
    margin-top: 25px;
  }
  100% {
    margin-top: 0px;
  }
}
@keyframes fade-up-small {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}
